import React,{Component} from 'react'
import axios from 'axios'

import { Carousel, WingBlank } from 'antd-mobile';
import 'antd-mobile/dist/antd-mobile.css'

import Swiper from 'swiper';
import "swiper/dist/css/swiper.css"

export default class Cart extends Component {
  constructor(props){
    super(props)  
    this.state={
      list:[], //影院数据
      list2:[], //电影数据
      date:'',
      list3:[]
    }   
  }
  getData(){
    axios.get('https://m.maizuo.com/gateway/?cinemaId='+this.props.match.params.id+'&k=3977516',{
      headers:{
        'X-Client-Info': `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        'X-Host': 'mall.film-ticket.cinema.info'
      }
    }).then((res)=>{
      this.setState({
        list:res.data.data.cinema
      })
    })
    axios.get('https://m.maizuo.com/gateway/?cinemaId=2536&k=5434923',{
      headers:{
        'X-Client-Info': `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        'X-Host': 'mall.film-ticket.film.cinema-show-film'
      }
    }).then((res)=>{
      this.setState({
        list2:res.data.data.films
      })
    })
    axios.get('https://m.maizuo.com/gateway/?filmId=4760&cinemaId=4954&date=1564329600&k=6503577',{
      headers:{
        'X-Client-Info': `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        'X-Host': 'mall.film-ticket.schedule.list'
      }
    }).then((res)=>{
      console.log(res)
      this.setState({
        list3:res.data.data.schedules
      })
    })
    var date = new Date().toLocaleString()
    this.setState({date}) 
  }
  componentDidMount(){
    this.getData()
  }
  componentWillUnmount(){
    this.setState = (state, callback)=>{
      return;
    }
  }
  componentDidUpdate(){
		var mySwiper = new Swiper ('.swiper-container', {
             loop: false,
             slidesPerGroup : 1,
             slidesPerView : 3
     			})       
	}
  render(){
    var dates = this.state.date
    return <div className="cart">
        <div className="cart-header">    
          <h3>{this.state.list.name}</h3>
          <span>地址:{this.state.list.address}</span>
          
        </div>

        <div className='cart-center'>
          <div className="swiper-container">
           <div className="swiper-wrapper">
            {
              this.state.list2.map((item)=>{
                return <div className="swiper-slide" key={item.filmId}>
                  
                   <img src={item.poster}/>
                  <p>{item.name}</p>
                </div>
              })
            }
           </div>
          </div> 
        </div>

        <div className="cart-footer">
          <h2>今天：{dates.slice(0,10)}</h2>
          {
            this.state.list3.map((item)=>{
              return <li key={item.scheduleId}>
                 <div className="date">
                  <p>{new Date(item.showAt*1000).toLocaleString().slice(10,16)}</p>
                  <p>{new Date(item.endAt*1000).toLocaleString().slice(10,16)}散场</p>
                 </div>
                 <div className="3d">
                  <p>{item.filmLanguage}{item.imagery}</p>
                  <p>{item.hallName}</p>
                 </div>
                 <div className="monly">
                  <span>￥{item.salePrice/100}</span>  
                 </div>
                 <div className="onsell">
                  <p>{item.isOnsell?'买票':'停售'}</p>
                 </div>               
              </li>
            })
          }    
        </div> 
    </div>
  }
}