import React,{Component} from 'react'

import Swiper from 'swiper';
import "swiper/dist/css/swiper.css"
import axios from 'axios'

import Being from './Being'
import About from './About'
import {NavLink,Route,Switch,Redirect} from 'react-router-dom'
export default class Home extends Component {
  constructor(props){
    super(props)
    this.state={
      list:[]
    }
  }
  componentDidMount(){
		this.getData();
		  
	}
  getData(){
    axios.get('https://m.maizuo.com/gateway?type=2&cityId=110100&k=5235516',{
      headers:{
				"X-Client-Info": `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563442745133143986751"}`,
				"X-Host": "mall.cfg.common-banner"
			}
    }).then((res)=>{
      this.setState({
        list:res.data.data
      })
    })
  }
  componentDidUpdate(){
		var mySwiper = new Swiper ('.swiper-container', {
     				loop: true,
    				autoplay: {
					    delay: 3000,					  
					    disableOnInteraction: false
    				}
     			})       
	}
  render(){
    return <div className="home">
              <div className="swiper-container">
                <div className="swiper-wrapper">
                  {
                    this.state.list.map((item)=>{
                      return <div className="swiper-slide" key={item.bannerId}>
                          <img src={item.imgUrl} style={{width:'100%'}}/>
                      </div>
                    })
                    
                  }
                    
                </div>     
                <div className="swiper-pagination"></div>    
              </div>

              <div className="ab">
                  <div className="txt">
                    <NavLink to="/home/Being">正在热映</NavLink>
                    <NavLink to="/home/About">即将上映</NavLink>
                  </div>
                  <Switch>
                    <Route path="/home/Being" component={Being} />
                    <Route path="/home/About" component={About} />
                    <Redirect from="/home" to="/home/being" exact />
                  </Switch>
                </div>
            </div>  
  }
}