import React,{Component} from 'react'
import axios from 'axios'
export default class List extends Component {
  constructor(props){
    super(props)
    this.state={
      list:[]
    }
  }
  componentDidMount(){
    this.getData()
  }
  componentWillUnmount(){
    this.setState = (state, callback)=>{
      return;
    }
  }
  getData(){
    axios.get('https://m.maizuo.com/gateway?cityId=110100&ticketFlag=1&k=3087402',{
      headers:{
        "X-Client-Info": `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        "X-Host": "mall.film-ticket.cinema.list"
      }
    }).then((res)=>{
  
      this.setState({
        list:res.data.data.cinemas
      })
    })
  }
  go(id){
    this.props.history.push('/Cart/'+id)
  }
  render(){
    return <div className='list'>
          <div className="yy"><span>影院</span></div>
          <ul>
            {
              this.state.list.map((item)=>{
              
                return <li key={item.cinemaId} onClick={this.go.bind(this,item.cinemaId)}>
                  <div>
                    <a>{item.name}</a>
                    <span>{item.address}</span>
                 </div>
                </li>
              })
            }
          </ul>
    </div>
  }
}