import React,{Component} from 'react'
import axios from 'axios'
export default class Detail extends Component {
  constructor(props){
    super(props)
    console.log(props)
    this.state={
      list:[]
    }
  }
  componentDidMount(){
  
    this.getData()
  }
  componentWillUnmount(){
    this.setState = (state, callback)=>{
      return;
    }
  }
  getData(){
    axios.get("https://m.maizuo.com/gateway?filmId="+this.props.match.params.id+"&k=7870636",{    
      headers:{
        "X-Client-Info": `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        "X-Host": "mall.film-ticket.film.info"
      }
    }).then((res)=>{
     
      this.setState({
        list:res.data.data.film
      })
    })
  }
  go(){
    this.props.history.push('/list')
  }
  render(){
    var lists = this.state.list
    return <div className="detail">
      <img src={lists.poster} />
      <div className="center">
        <p>{lists.name}</p>
        <p>{lists.category}</p>
        <span>{lists.nation}</span>|<span>{lists.runtime}分钟</span>
        <p>{lists.synopsis}</p>
        <div onClick={this.go.bind(this)}>选座购票</div>       
      </div>
    </div>
  }
}