import React,{Component} from 'react';
import axios from 'axios'
export default class List extends Component {
  constructor(props){
    super(props);
    this.state={
      list:[]
    }
  }
  componentDidMount(){
    this.getDate()
}
  getDate(){
    axios.get('https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=2&k=659265',{
      headers:{
        "X-Client-Info": `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        "X-Host": "mall.film-ticket.film.list"
      }
    }).then((res)=>{
     
      this.setState({
        list:res.data.data.films
      })
    })
  }
  componentWillUnmount(){
    this.setState = (state, callback)=>{
      return;
    }
  }
  render(){
    return <div className="ac">正在热映
             {
               this.state.list.map((item)=>{
                 return <li key={item.filmId}>
                 <img src={item.poster} />
                   {item.name}                 
                 </li>
               })
             }
           </div>
  }
}