import React,{Component} from 'react'

import { List } from 'antd-mobile';
import 'antd-mobile/dist/antd-mobile.css'
const Item = List.Item;
const Brief = Item.Brief;

export default class My extends Component {
  constructor(props){
    super(props);
    this.state = {
      disabled: false,
    }  
  }
  go(){
    var a = document.querySelector('.top1 a')
    this.props.history.push('/login')
    if(sessionStorage.user==123){
      a.innerHTML=123
    }
  }
  render(){
    return <div className="my">
      <div className="my-top">
        <div className="top1">
          <i className="iconfont">&#xe60c;</i>
          <a onClick={this.go.bind(this)}
          >立即登录</a>
        </div>
      </div>

      <div className="my-center">
        <div className="wrap">
          <div className="cen1"><span><i className="iconfont">&#xe738;</i>电影订单</span></div>
          <div className="cen2"><span><i className="iconfont">&#xe7fd;</i>商品订单</span></div>
        </div>
      </div>

      <div className="my-bottom">
      <List className="my-list">
        <Item extra={'￥0'}>
        <i className="iconfont">&#xe62e;</i>
        余额
        </Item>
      </List>
      <List className="my-list">
        <Item arrow="horizontal" multipleLine onClick={() => {}}>
        <i className="iconfont">&#xe630;</i>
        领劵 
        </Item>
        <Item
          arrow="horizontal"
          multipleLine
          onClick={() => {}}
          platform="android"
        >
          <i className="iconfont">&#xe61d;</i>
          红包
        </Item>
        <Item
          arrow="horizontal"
          multipleLine
          onClick={() => {}}
          platform="android"
        >
        <i className="iconfont">&#xe600;</i>
         设置
        </Item>
       
      </List>

      </div>
    </div>
  }
}