import React,{Component} from 'react';
import axios from 'axios'
import { Button, WhiteSpace, WingBlank } from 'antd-mobile';
import 'antd-mobile/dist/antd-mobile.css'



export default class List extends Component {
  constructor(props){
    super(props);
    this.state={
      list:[]
    }
  }
  componentDidMount(){
    this.getData()
  }
  componentWillUnmount(){
    this.setState = (state, callback)=>{
      return;
    }
  }
  getData(){
    axios.get('https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=1&k=3540840',{
      headers:{
        "X-Client-Info": `{"a":"3000","ch":"1002","v":"5.0.4","e":"1563449911137438954616"}`,
        "X-Host": "mall.film-ticket.film.list"
      }
    }).then((res)=>{
      
      this.setState({
        list:res.data.data.films
      })
    })
  }
 go(id){
  this.props.history.push("/detail/"+id);
 }
  render(){
    return <div className="ab1">
      {
        this.state.list.map((item)=>{
          return <li key={item.filmId}>
          <img src={item.poster} />
          <div className="ab2">
            <span>{item.name}</span>
            <span>主演：{item.director}</span>
            <span>{item.nation}|{item.runtime}分钟</span>
            <Button onClick={this.go.bind(this,item.filmId)} className="prefixCls" type="ghost" inline size="small" style={{ marginRight: '4px' }}>购票</Button>
          </div>
          </li>
        })
      }
    </div>
  }
}